<template>
	<view v-if="show" class="uni-noticebar" :style="{ backgroundColor: backgroundColor }">
		<view ref="textBox" class="uni-noticebar__content-wrapper"
			:class="{'uni-noticebar__content-wrapper--scrollable':scrollable, 'uni-noticebar__content-wrapper--single'}">
			<view :id="elIdBox" class="uni-noticebar__content"
				:class="{'uni-noticebar__content--scrollable':scrollable, 'uni-noticebar__content--single'}">
				<text :id="elId" ref="animationEle" class="uni-noticebar__content-text"
					:class="{'uni-noticebar__content-text--scrollable':scrollable,'uni-noticebar__content-text--single'}"
					:style="{color:color, width:wrapWidth+'px','animationDuration': animationDuration, '-webkit-animationDuration': animationDuration ,animationPlayState: webviewHide?'paused':animationPlayState,'-webkit-animationPlayState':webviewHide?'paused':animationPlayState, animationDelay: animationDelay, '-webkit-animationDelay':animationDelay}">{{text}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	const dom = weex.requireModule('dom');
	const animation = weex.requireModule('animation');

	/**
	 * NoticeBar 自定义导航栏
	 * @description 通告栏组件
	 * @tutorial https://ext.dcloud.net.cn/plugin?id=30
	 * @property {Number} speed 文字滚动的速度，默认100px/秒
	 * @property {String} text 显示文字
	 * @property {String} backgroundColor 背景颜色
	 * @property {String} color 文字颜色
	 * @property {Boolean} scrollable = [true|false] 是否滚动，为true时，NoticeBar为单行
	 */

	export default {
		name: 'UniNoticeBar',
		props: {
			text: {
				type: String,
				default: ''
			},
			backgroundColor: {
				type: String,
				default: '#fff'
			},
			speed: {
				// 默认1s滚动100px
				type: Number,
				default: 100
			},
			color: {
				type: String,
				default: '#000'
			},
			scrollable: {
				// 是否滚动，添加后控制单行效果取消
				type: [Boolean, String],
				default: false
			},
		},
		data() {
			const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
			const elIdBox = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
			return {
				textWidth: 0,
				boxWidth: 0,
				wrapWidth: '',
				webviewHide: false,
				stopAnimation: false,
				elId: elId,
				elIdBox: elIdBox,
				show: true,
				animationDuration: 'none',
				animationPlayState: 'paused',
				animationDelay: '0s'
			}
		},
		mounted() {
			var pages = getCurrentPages();
			var page = pages[pages.length - 1];
			var currentWebview = page.$getAppWebview();
			currentWebview.addEventListener('hide', () => {
				this.webviewHide = true
			})
			currentWebview.addEventListener('show', () => {
				this.webviewHide = false
			})
			this.$nextTick(() => {
				this.initSize()
			})
		},
		beforeDestroy() {
			this.stopAnimation = true
		},
		methods: {
			initSize() {
				if (this.scrollable) {
					let query = [],
						boxWidth = 0,
						textWidth = 0;
					let textQuery = new Promise((resolve, reject) => {
						uni.createSelectorQuery()
							.select(`#${this.elId}`)
							.boundingClientRect()
							.exec(ret => {
								this.textWidth = ret[0].width
								resolve()
							})
					})
					let boxQuery = new Promise((resolve, reject) => {
						uni.createSelectorQuery()
							.select(`#${this.elIdBox}`)
							.boundingClientRect()
							.exec(ret => {
								this.boxWidth = ret[0].width
								resolve()
							})
					})
					query.push(textQuery)
					query.push(boxQuery)
					Promise.all(query).then(() => {
						this.animationDuration = `${this.textWidth / this.speed}s`
						this.animationDelay = `-${this.boxWidth / this.speed}s`
						setTimeout(() => {
							this.animationPlayState = 'running'
						}, 1000)
					})
					dom.getComponentRect(this.$refs['animationEle'], (res) => {
						let winWidth = uni.getSystemInfoSync().windowWidth
						this.textWidth = res.size.width
						animation.transition(this.$refs['animationEle'], {
							styles: {
								transform: `translateX(-${winWidth}px)`
							},
							duration: 0,
							timingFunction: 'linear',
							delay: 0
						}, () => {
							if (!this.stopAnimation) {
								animation.transition(this.$refs['animationEle'], {
									styles: {
										transform: `translateX(-${this.textWidth}px)`
									},
									timingFunction: 'linear',
									duration: (this.textWidth - winWidth) / this.speed * 1000,
									delay: 1000
								}, () => {
									if (!this.stopAnimation) {
										this.loopAnimation()
									}
								});
							}
						});
					})
				}
				if (!this.scrollable) {
					dom.getComponentRect(this.$refs['textBox'], (res) => {
						this.wrapWidth = res.size.width
					})
				}
			},
			loopAnimation() {
				animation.transition(this.$refs['animationEle'], {
					styles: {
						transform: `translateX(0px)`
					},
					duration: 0
				}, () => {
					if (!this.stopAnimation) {
						animation.transition(this.$refs['animationEle'], {
							styles: {
								transform: `translateX(-${this.textWidth}px)`
							},
							duration: this.textWidth / this.speed * 1000,
							timingFunction: 'linear',
							delay: 0
						}, () => {
							if (!this.stopAnimation) {
								this.loopAnimation()
							}
						});
					}
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
	.uni-noticebar {
		display: flex;
		width: 100%;
		box-sizing: border-box;
		flex-direction: row;
		align-items: center;
		padding: 6px 12px;
		margin-bottom: 10px;
	}

	.uni-noticebar__content-wrapper {
		flex: 1;
		flex-direction: column;
		overflow: hidden;
	}

	.uni-noticebar__content-wrapper--single {
		line-height: 18px;
	}

	.uni-noticebar__content-wrapper--single,
	.uni-noticebar__content-wrapper--scrollable {
		flex-direction: row;
	}

	.uni-noticebar__content-wrapper--scrollable {
		position: relative;
		height: 18px;
	}

	.uni-noticebar__content--scrollable {
		flex: 1;
		display: block;
		overflow: hidden;
	}

	.uni-noticebar__content--single {
		display: flex;
		flex: none;
		width: 100%;
		justify-content: center;
	}

	.uni-noticebar__content-text {
		font-size: 14px;
		line-height: 18px;
		word-break: break-all;
	}

	.uni-noticebar__content-text--single {
		lines: 1;
		display: block;
		width: 100%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.uni-noticebar__content-text--scrollable {
		lines: 1;
		padding-left: 750rpx;
		position: absolute;
		display: block;
		height: 18px;
		line-height: 18px;
		white-space: nowrap;
		padding-left: 100%;
		animation: notice 10s 0s linear infinite both;
		animation-play-state: paused;
	}

	@keyframes notice {
		100% {
			transform: translate3d(-100%, 0, 0);
		}
	}
</style>
